/*---------------------------------------------------*/
.leaderboard-card-first {
  /* background-image: linear-gradient(180deg, #ffcc00 20%, #ffa500 80%); */
  background-color: rgba(255, 215, 0, 0);
  background-image: linear-gradient(180deg, #ffcc00 20%, #ffa500 80%);
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.8) !important;
  transition: all 0.5s ease;
}

.leaderboard-card-second {
  /* background-image: linear-gradient(180deg, #fefefe 20%, #9f9f9f 80%); */
  background-color: rgba(130, 185, 186, 0);
  background-image: linear-gradient(180deg, #fefefe 20%, #9f9f9f 80%);
  box-shadow: 0 4px 12px 0 #ffffff !important;
  transition: all 0.5s ease;
}

.leaderboard-card-third {
  /* background-image: linear-gradient(180deg, #d58d45 20%, #9a4e17 80%); */
  background-color: rgba(225, 152, 79, 0);
  background-image: linear-gradient(180deg, #d58d45 20%, #9a4e17 80%);
  box-shadow: 0 4px 12px 0 rgba(205, 127, 50, 0.8) !important;
  transition: all 0.5s ease;
}

.leaderboard-cards-container {
  display: flex;
  justify-content: space-around;
  margin: 20px 0;
  scale: 0.9;
}
.leaderboard-cards {
  /* width: 400px; */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  min-height: 120px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  height: 100%;
}

.leaderboard-items {
  width: 30%;
}

.leaderboard-card_heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.leaderboard-card_heading .numberds {
  font-size: 35px;
  font-weight: 500;
  /* color: #07070; */
}

.leaderboard-card_heading .nameds {
  color: #000;
}
