@media only screen and (max-width: 900px) {
  .hello {
    width: 350px;
  }
}
@media only screen and (max-width: 700px) {
  .hello {
    width: 300px;
  }

  .leaderboard-cards-container {
    scale: 1;
  }
  .leaderboard-card_heading {
    padding: 0px;
    margin: -1px;
  }
  .leaderboard-card_heading .numberds,
  .leaderboard-card_heading .nameds {
    font-size: 20px;
    font-weight: 500;
  }
  .leaderboard-cards {
    /* width: 200px; */
    /* width: 100; */
    padding: 4px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
    min-height: 60px;
  }

  .listing {
    width: 80%;
    margin-left: 10%;
  }
}
@media only screen and (max-width: 500px) {
  .leaderboard-items {
    width: 80%;
  }

  .leaderboard-cards-container {
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
  }

  .leaderboard-cards {
    margin: 10px auto 0 auto;
    height: 150px;
  }

  .list_items {
    font-size: 15px;
  }
  .list_item_on {
    font-size: 15px;
    font-weight: 30000;
  }
  .leaderboard_heading h2 {
    font-size: 30px !important;
    margin-bottom: 20px;
  }
  .name_score_listing {
    margin: 3%;
    margin-bottom: 1%;
    padding-right: 5%;
    width: 78%;
  }
  .listing {
    height: 50px;
    margin-inline: 5%;
    width: 95%;
    margin-block: 0px;
  }

  [class="1"],
  [class="2"],
